.no-suggestions {
    background-color: #fff;
    max-height: 45px;
    border: 1px solid #8c8c8c;
    border-radius: 5px;
    z-index: 10;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.no-suggestions > em {
    padding-left: 1rem;
    font-weight: 700;
    color: #737373;
}

.suggestions {
    background-color: #fff;
    list-style: none;
    margin-top: 0;
    max-height: 143px;
    overflow-y: auto;
    padding-left: 0;
    width: 100%;
    z-index: 10;
    border-radius: 5px;
}

.suggestions li {
    padding: 0.5rem;
    min-height: 45px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    border: 1px solid #8c8c8c;
    border-top: none;
}

.suggestions li:first-child {
    border: 1px solid #8c8c8c;
}

.suggestions li:last-child {
    border-radius: 0 0 5px 5px;
}

.suggestions li > a {
    text-decoration: none !important;
    padding-left: 1rem;
    color: #8c8c8c;
}

.suggestions li > a:hover {
    text-decoration: none !important;
}

.suggestions li:hover {
    background-color: #f6f6f6;
    cursor: pointer;
    font-weight: 700;
    color: #737373;
}

